import { LinearScale, LogScale, PowScale, SequantialScale, SqrtScale, TimeScale, UtcScale } from './quantitative';
import { BandScale, OrdinalScale, PointScale } from './discrete';
import { QuantizeScale } from './QuantizeScale';
import { QuantileScale } from './QuantileScale';
export * from './quantitative';
export * from './discrete';
export * from './QuantizeScale';
export * from './QuantileScale';
export { CategoricalColorScheme } from './colorSchemeMap'; // Quantitative Scale Factories

export var linear = function linear(name) {
  return new LinearScale().name(name);
};
export var log = function log(name) {
  return new LogScale().name(name);
};
export var pow = function pow(name) {
  return new PowScale().name(name);
};
export var sequential = function sequential(name) {
  return new SequantialScale().name(name);
};
export var sqrt = function sqrt(name) {
  return new SqrtScale().name(name);
};
export var time = function time(name) {
  return new TimeScale().name(name);
};
export var utc = function utc(name) {
  return new UtcScale().name(name);
}; // Discrete Scale Factories

export var band = function band(name, bandwidth) {
  return new BandScale().name(name).bandwidthName(bandwidth);
};
export var ordinal = function ordinal(name) {
  return new OrdinalScale().name(name);
};
export var point = function point(name, step) {
  return new PointScale().name(name).stepName(step);
}; // Other

export var quantize = function quantize(name) {
  return new QuantizeScale().name(name);
};
export var quantile = function quantile(name) {
  return new QuantileScale().name(name);
};